import React from 'react';
import PropTypes from 'prop-types';
import RetailRegionStaticComponent from './RetailRegionStaticComponent';
import RetailRegionFormEditableComponent from './RetailRegionFormEditableComponent';
import RetailRegionConfirmComponent from './RetailRegionConfirmComponent';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import { RETAIL_REGION_FORM_STEPS } from '../../../../constants/userSettingsFormConstants';

import './RetailRegionFormComponent.css';

const classes = getBEMClasses('retail-region-form');

const RetailRegionFormComponent = props => {
  const {
    handleSubmit,
    formObj: { step }
  } = props;

  return (
    <form className={classes()} onSubmit={handleSubmit}>
      {step && getStepsMap(props)[step]()}
    </form>
  );
};

const getStepsMap = props => {
  return {
    [RETAIL_REGION_FORM_STEPS.STATIC]: () => (
      <RetailRegionStaticComponent
        language={props.formObj.language}
        languageNames={props.languageNames}
        goToStep={props.goToStep}
      />
    ),
    [RETAIL_REGION_FORM_STEPS.EDIT]: () => (
      <RetailRegionFormEditableComponent
        language={props.formObj.language}
        translations={props.translations}
        languageNames={props.languageNames}
        products={props.products}
        goToStep={props.goToStep}
      />
    ),
    [RETAIL_REGION_FORM_STEPS.CONFIRM]: () => (
      <RetailRegionConfirmComponent
        regionFormValues={props.formObj}
        languageNames={props.languageNames}
      />
    )
  };
};

RetailRegionFormComponent.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  goToStep: PropTypes.func.isRequired,
  editIsDisabled: PropTypes.bool,
  formObj: PropTypes.object
};

export default RetailRegionFormComponent;



