import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../../../common/APButton';
import APLabelWithText from '../../../common/APLabelWithText/APLabelWithText';
import ContentWithFooterLayoutTemplate from '../../../common/LayoutTemplates/ContentWithFooterLayoutTemplate/ContentWithFooterLayoutTemplate';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';

import './RetailRegionConfirmComponent.css';

const baseClass = 'retail-region-confirm';
const retailRegionConfirmClasses = getBEMClasses(baseClass);

const RetailRegionConfirm = (props, context) => {
  const { regionFormValues, languageNames } = props;

  const footer = (
    <APButton customClass={retailRegionConfirmClasses()} type="submit">
      {context.t('Update')}
    </APButton>
  );

  return (
    <div className={retailRegionConfirmClasses()}>
      <ContentWithFooterLayoutTemplate
        footer={footer}
        customClass={`${baseClass}-cwf-lt`}>
        <React.Fragment>
          <div className={retailRegionConfirmClasses('confirm-block')}>
            {context.t('Confirm')}
          </div>

          <APLabelWithText
            label={context.t('Language')}
            text={languageNames[regionFormValues.language]}
            customClass={retailRegionConfirmClasses()}
          />
        </React.Fragment>
      </ContentWithFooterLayoutTemplate>
    </div>
  );
};

RetailRegionConfirm.propTypes = {
  regionFormValues: PropTypes.object.isRequired
};

RetailRegionConfirm.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailRegionConfirm;



