import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../../../common/APButton';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';

import './RetailResetPasswordComponent.css';

const retailResetPasswordFormClasses = getBEMClasses(
  'retail-reset-password-form'
);

const RetailResetPasswordConfirmComponent = (props, context) => {
  const { submitting } = props;

  return (
    <div>
      <div className={retailResetPasswordFormClasses('text-info')}>
        {context.t(
          'Check your email with instructions on changing your password'
        )}
      </div>

      <div className={retailResetPasswordFormClasses('button-container')}>
        <APButton type="submit" customClass={retailResetPasswordFormClasses()}>
          {submitting ? context.t('Processing...') : context.t('Send Email')}
        </APButton>
      </div>
    </div>
  );
};

RetailResetPasswordConfirmComponent.propTypes = {
  submitting: PropTypes.bool
};

RetailResetPasswordConfirmComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailResetPasswordConfirmComponent;



