import React from 'react';
import PropTypes from 'prop-types';
import APIcon from '../../common/APIcon/APIcon';
import ResetPasswordRequestFormContainerFactory from '../../ResetPasswordRequestForm/ResetPasswordRequestFormContainerFactory';
import EnableTwoFactorAuthContainerFactory from '../../EnableTwoFactorAuth/EnableTwoFactorAuthContainerFactory';
import RetailResetPasswordFormComponent from './ResetPasswordForm/RetailResetPasswordFormComponent';
import ContentWithHeaderLayoutTemplate from '../../common/LayoutTemplates/ContentWithHeaderLayoutTemplate/ContentWithHeaderLayoutTemplate';
import RetailEnableTwoFactorAuthComponent from './RetailEnableTwoFactorAuthComponent';
import RetailProfileFormContainer from './ProfileForm/RetailProfileFormContainer';
import RetailRegionFormContainer from './RegionForm/RetailRegionFormContainer';
import { RETAIL_PROFILE_FORM_STEPS } from '../../../constants/userSettingsFormConstants';
import { RETAIL_RESET_PASSWORD_STEPS } from '../../../constants/userSettingsFormConstants';
import { RETAIL_REGION_FORM_STEPS } from '../../../constants/userSettingsFormConstants';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';

import './RetailProfileAndSettingsComponent.css';

const retailProfileAndSettingsClasses = getBEMClasses([
  'retail-profile-and-settings-layout'
]);
const retailProfileAndSettingWidgetClasses = getBEMClasses([
  'retail-profile-and-settings-widget'
]);

const EnableTwoFactorAuthContainer = EnableTwoFactorAuthContainerFactory(
  RetailEnableTwoFactorAuthComponent
);
const ResetPasswordFormContainer = ResetPasswordRequestFormContainerFactory(
  RetailResetPasswordFormComponent
);

class RetailProfileAndSettingsComponent extends React.Component {
  static contextTypes = {
    t: PropTypes.func.isRequired
  };

  render() {
    const {
      profileForm,
      regionForm,
      resetPasswordRequest,
      goToProfileFormStep,
      goToRegionFormStep,
      goToResetPasswordFormStep
    } = this.props;

    return (
      <div className={retailProfileAndSettingsClasses('main')}>
        <div className={retailProfileAndSettingsClasses('info')} />
        <div className={retailProfileAndSettingsClasses('body')}>
          <div className={retailProfileAndSettingsClasses('page-header')}>
            <h1 className={retailProfileAndSettingsClasses('title')}>
              {this.context.t('Update Profile and Security Settings.')}
            </h1>
            <h2 className={retailProfileAndSettingsClasses('sub-title')}>
              {this.context.t(' Here you can make general settings such as...')}
            </h2>
          </div>
          <div className={retailProfileAndSettingsClasses('row')}>
            <ContentWithHeaderLayoutTemplate
              header={getLayoutHeader(
                this.context.t('Profile'),
                profileForm.values.step !== RETAIL_PROFILE_FORM_STEPS.STATIC,
                () => goToProfileFormStep(RETAIL_PROFILE_FORM_STEPS.STATIC),
                retailProfileAndSettingWidgetClasses
              )}
              customClass="retail-profile-and-settings-widget">
              <RetailProfileFormContainer />
            </ContentWithHeaderLayoutTemplate>

            <ContentWithHeaderLayoutTemplate
              header={getLayoutHeader(
                this.context.t('Region'),
                regionForm.values.step !== RETAIL_REGION_FORM_STEPS.STATIC,
                () => goToRegionFormStep(RETAIL_REGION_FORM_STEPS.STATIC),
                retailProfileAndSettingWidgetClasses
              )}
              customClass="retail-profile-and-settings-widget">
              <RetailRegionFormContainer />
            </ContentWithHeaderLayoutTemplate>
          </div>

          <div className={retailProfileAndSettingsClasses('row', 'sm')}>
            <ContentWithHeaderLayoutTemplate
              header={getLayoutHeader(
                this.context.t('Password'),
                resetPasswordRequest.values.step !==
                  RETAIL_RESET_PASSWORD_STEPS.STATIC,
                () =>
                  goToResetPasswordFormStep(RETAIL_RESET_PASSWORD_STEPS.STATIC),
                retailProfileAndSettingWidgetClasses
              )}
              customClass="retail-profile-and-settings-widget"
              classModifiers={['sm']}>
              <ResetPasswordFormContainer customClass="retail-reset-password-form" />
            </ContentWithHeaderLayoutTemplate>

            <ContentWithHeaderLayoutTemplate
              header={this.context.t('Two-Factor Authentication')}
              customClass="retail-profile-and-settings-widget"
              classModifiers={['sm']}>
              <EnableTwoFactorAuthContainer />
            </ContentWithHeaderLayoutTemplate>
          </div>
        </div>
      </div>
    );
  }
}

const getLayoutHeader = (header, closeIcon = false, close, classes) => {
  return (
    <div className={classes('header')}>
      <div className={classes('header-text')}>{header}</div>
      {closeIcon && (
        <div onClick={close}>
          <APIcon
            name="close"
            customClass={classes('icon')}
            classModifiers="big"
          />
        </div>
      )}
    </div>
  );
};

RetailProfileAndSettingsComponent.propTypes = {
  profileForm: PropTypes.object.isRequired,
  regionForm: PropTypes.object.isRequired,
  resetPasswordRequest: PropTypes.object.isRequired,
  goToProfileFormStep: PropTypes.func.isRequired,
  goToRegionFormStep: PropTypes.func.isRequired,
  goToResetPasswordFormStep: PropTypes.func.isRequired
};

export default RetailProfileAndSettingsComponent;



