import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import RetailRegionFormComponent from './RetailRegionFormComponent';
import { setUserRegionConfig } from '../../../../redux/actions/regionFormActions';
import { RETAIL_REGION_FORM_STEPS } from '../../../../constants/userSettingsFormConstants';
import { goToRegionFormStep } from '../../../../redux/actions/regionFormActions';
import { isRegionSectionEditingDisabled } from '../../../../redux/selectors/userSelectors';
import config from '../../../../config';

const RetailRegionForm = reduxForm({
  form: 'userProfileRegion',
  enableReinitialize: true,
  onSubmit: (payload, dispatch) => dispatch(setUserRegionConfig(payload))
})(RetailRegionFormComponent);

const mapStateToProps = state => {
  const { form } = state;
  const { languageNames } = config.Language;
  const editIsDisabled = isRegionSectionEditingDisabled(state);
  return {
    initialValues: {
      language: state.i18nState.lang,
      step: RETAIL_REGION_FORM_STEPS.STATIC
    },
    formObj: form.userProfileRegion ? form.userProfileRegion.values : {},
    translations: state.i18nState.translations,
    languageNames,
    editIsDisabled
  };
};

const mapDispatchToProps = {
  goToStep: goToRegionFormStep
};

const RetailRegionFormContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(RetailRegionForm);

export default RetailRegionFormContainer;



