import React from 'react';
import PropTypes from 'prop-types';
import APSelect from '../../../common/APSelect/APSelect';
import APButton from '../../../common/APButton';
import ContentWithFooterLayoutTemplate from '../../../common/LayoutTemplates/ContentWithFooterLayoutTemplate/ContentWithFooterLayoutTemplate';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import { RETAIL_REGION_FORM_STEPS } from '../../../../constants/userSettingsFormConstants';

import './RetailRegionFormEditableComponent.css';

const retailRegionEditableClasses = getBEMClasses('retail-region-editable');

const RetailRegionFormEditable = (props, context) => {
  const { goToStep, translations, languageNames } = props;

  let languageOptions = [{ value: 'en', label: 'English' }];

  if (typeof translations === 'object') {
    const availableLangs = Object.keys(translations).filter(
      i => i !== 'options'
    );
    if (availableLangs.length) {
      languageOptions = availableLangs.map(value => ({
        value,
        label: languageNames[value]
      }));
    }
  }

  const footer = (
    <APButton
      customClass={retailRegionEditableClasses()}
      onClick={() => goToStep(RETAIL_REGION_FORM_STEPS.CONFIRM)}>
      {context.t('Next')}
    </APButton>
  );

  return (
    <div className={retailRegionEditableClasses()}>
      <ContentWithFooterLayoutTemplate
        footer={footer}
        customClass="retail-region-editable-cwf-lt">
        <React.Fragment>
          <div className={retailRegionEditableClasses('input-wrapper')}>
            <APSelect
              name="language"
              label={context.t('Language')}
              customClass={retailRegionEditableClasses()}
              options={languageOptions}
            />
          </div>
        </React.Fragment>
      </ContentWithFooterLayoutTemplate>
    </div>
  );
};

RetailRegionFormEditable.propTypes = {
  languageNames: PropTypes.object.isRequired,
  translations: PropTypes.object.isRequired,
  goToStep: PropTypes.func.isRequired
};

RetailRegionFormEditable.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailRegionFormEditable;



