import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../../../common/APButton';
import APLabelWithText from '../../../common/APLabelWithText/APLabelWithText';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import { RETAIL_REGION_FORM_STEPS } from '../../../../constants/userSettingsFormConstants';

import './RetailRegionStaticComponent.css';

const retailRegionStaticClasses = getBEMClasses('retail-region-static');

const RetailRegionStaticComponent = (props, context) => {
  const { goToStep, language, languageNames } = props;

  return (
    <div className={retailRegionStaticClasses()}>
      <div className={retailRegionStaticClasses('row')}>
        <APLabelWithText
          label={context.t('Language')}
          text={languageNames[language]}
          customClass={retailRegionStaticClasses()}
        />
      </div>

      <div className={retailRegionStaticClasses('button-container')}>
        <APButton
          customClass={retailRegionStaticClasses()}
          onClick={() => goToStep(RETAIL_REGION_FORM_STEPS.EDIT)}>
          {context.t('Edit')}
        </APButton>
      </div>
    </div>
  );
};

RetailRegionStaticComponent.defaultProps = {};

RetailRegionStaticComponent.propTypes = {
  language: PropTypes.string.isRequired,
  customClass: PropTypes.string,
  goToStep: PropTypes.func.isRequired
};

RetailRegionStaticComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailRegionStaticComponent;



