import { change } from 'redux-form';
import { changeLanguage } from './languageActions';
import { RETAIL_REGION_FORM_STEPS } from '../../constants/userSettingsFormConstants';

export const goToRegionFormStep = step => dispatch =>
  dispatch(change('userProfileRegion', 'step', step));

export const setUserRegionConfig = () => (dispatch, getState) => {
  const { values } = getState().form.userProfileRegion;

  dispatch(changeLanguage(values.language));
  dispatch(
    change('userProfileRegion', 'step', RETAIL_REGION_FORM_STEPS.STATIC)
  );
};



