import React from 'react';
import PropTypes from 'prop-types';
import APButton from '../../../common/APButton';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import { RETAIL_RESET_PASSWORD_STEPS } from '../../../../constants/userSettingsFormConstants';

import './RetailResetPasswordComponent.css';

const retailResetPasswordFormClasses = getBEMClasses(
  'retail-reset-password-form'
);

const RetailResetPasswordStaticComponent = (props, context) => {
  const { goToStep } = props;

  return (
    <div>
      <div className={retailResetPasswordFormClasses('text-info')}>
        {context.t(
          'To change you password, we will send you an email that will allow you to access out Reset Password feature. Click the button above to receive the email.'
        )}
      </div>

      <div className={retailResetPasswordFormClasses('button-container')}>
        <APButton
          type="button"
          customClass={retailResetPasswordFormClasses()}
          onClick={() => goToStep(RETAIL_RESET_PASSWORD_STEPS.CONFIRM)}>
          {context.t('Reset Your Password')}
        </APButton>
      </div>
    </div>
  );
};

RetailResetPasswordStaticComponent.propTypes = {
  goToStep: PropTypes.func.isRequired
};

RetailResetPasswordStaticComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default RetailResetPasswordStaticComponent;



