import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../../../helpers/cssClassesHelper';
import RetailResetPasswordStaticComponent from './RetailResetPasswordStaticComponent';
import RetailResetPasswordConfirmComponent from './RetailResetPasswordConfirmComponent';

import './RetailResetPasswordFormComponent.css';

import { RETAIL_RESET_PASSWORD_STEPS } from '../../../../constants/userSettingsFormConstants';

const retailResetPasswordFormClasses = getBEMClasses(
  'retail-reset-password-form'
);

const RetailResetPasswordFormComponent = props => {
  const {
    handleSubmit,
    formObj: { step },
    submitting,
    goToStep
  } = props;

  return (
    <form className={retailResetPasswordFormClasses()} onSubmit={handleSubmit}>
      {step && getStepsMap(submitting, goToStep)[step]()}
    </form>
  );
};

const getStepsMap = (submitting, goToStep) => {
  return {
    [RETAIL_RESET_PASSWORD_STEPS.STATIC]: () => (
      <RetailResetPasswordStaticComponent goToStep={goToStep} />
    ),
    [RETAIL_RESET_PASSWORD_STEPS.CONFIRM]: () => (
      <RetailResetPasswordConfirmComponent submitting={submitting} />
    )
  };
};

RetailResetPasswordFormComponent.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  goToStep: PropTypes.func.isRequired,
  submitting: PropTypes.bool
};

export default RetailResetPasswordFormComponent;



